<template>
 <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
        <!-- <div class="center_content_title">
            <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
            <span>{{pageTitle}}</span>
        </div> -->
        <div class="center_content_search">
            <div class="center_content_search_left">
                <div class="search-input-suffix gap">
                    <label>目录筛选：</label>
                    <el-cascader
                      placeholder="请选择"
                      :options="directoryProgramOptions"
                      :show-all-levels="false"
                      change-on-select
                      :props='menuProps'
                      v-model="pId"
                    ></el-cascader>
                    <!-- <el-select v-model="page.parentId" placeholder="请选择">
                        <el-option
                        v-for="item in directoryProgramOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                        </el-option>
                    </el-select> -->
                </div>
                <div class="search-input-suffix">
                    <label>搜索：</label>
                    <el-input class="search"
                            @keyup.enter.native="search"
                            v-model="page.keywords"
                            placeholder="请输入搜索关键词"
                            clearable
                            >
                    </el-input>
                </div>
                <div class="search-input-suffix" style="margin: 0 0 0 20px">
                  <div class="search--btn"  @click="search">搜索</div>
                </div>
            </div>
        </div>
         <div class="table-container">
             <el-table
                :data="tableData"
                stripe>
                <el-table-column
                prop="name"
                label="目录名称">
                </el-table-column>
                <el-table-column
                prop="creatorVal"
                label="创建人">
                </el-table-column>
                <el-table-column
                prop="createDate"
                label="创建时间">
                </el-table-column>
                <el-table-column
                prop="url"
                label="目录链接" width="300">
                </el-table-column>
                <el-table-column
                label="操作" width="160">
                <template slot-scope="scope">
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconbianji1"></SvgIcon>
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        删除
                        </el-button>
                    </div>                    
                </template>
                </el-table-column>
            </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="data.total">
                </el-pagination>
            </div>
        </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog :title="dialogTitle" :visible.sync="userDialogFormVisible" :before-close="handleDialogClose" class="set-el-dialog" :close-on-click-modal='false'>
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="菜单名称" prop="name">
            <el-input v-model="form.name"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="菜单排序" prop="sort">
            <el-input v-model="form.sort"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="Icon" prop="icon">
            <el-input v-model="form.icon"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="菜单类型" prop="type">
             <el-radio-group v-model="form.type">
                <el-radio :label="0">菜单</el-radio>
                <el-radio :label="1">权限</el-radio>              
            </el-radio-group>
            </el-form-item>
             <el-form-item label="链接地址" prop="url">
            <el-input v-model="form.url"  placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="上级目录" prop="parentId">
            <el-cascader
                :options="menuOptions"
                 v-model="menuSelectedOptions"
                 :props='menuProps'>
            </el-cascader>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button  @click="cancel('form')">取 消</el-button>
            <el-button type="primary" @click="create('form')">确 定</el-button>
        </div>
    </el-dialog>
 </div>
</template>

<script>
import systemApi from '@/api/systemApi'
export default {
 name: 'DirectoryManagement',
 data() {
  const checkParentId = (rule, value, callback) => {
        if(this.menuSelectedOptions.length ===0){
            return callback(new Error('请选择上级目录'));
        }else{
            callback();
        }
  }
 return {
    pageTitle:'目录管理',
    data:{},
    tableData:[],
    isAdd:true,
    userDialogFormVisible:false,
    sysMenuAllArr:[],
    menuOptions:[],
    menuProps: {
          value: 'id',
          label:'name'
        },
    menuSelectedOptions:[],
    form: {
        name: '',
        url: '',
        parentId: '',
        type: 0,
        sort: '',
        code:'null',
        id:undefined,
        icon: ''
    },
    directoryProgramOptions: [],
    pId: [],
    page:{
        parentId:'',
        keywords:'',
        page:1,
        rows:15
    },
    rules:{
         name: [
            {
              required: true,
              message: '请输入菜单名称',
              trigger: 'blur'
            }
          ],
         url: [
            {
              required: true,
              message: '请输入链接地址',
              trigger: 'blur'
            }
          ],
         parentId: [
            {
              required: true,
              validator: checkParentId,
              trigger: 'blur'
            }
          ],
         type: [
            {
              required: true,
              message: '请选择菜单类型',
              trigger: 'blur'
            }
          ]
    }
 }
 },
 created(){},
 mounted(){
     this.initData()
    //  this.sysMenuAll();
      this.sysMenuAll1()
 },
 computed:{
     dialogTitle(){
        return this.isAdd ? '添加目录' : '编辑目录'
     },
     menuType() {
       return this.form.type
     }
 },
 watch: {
   menuType(val) {
     if(val) {
       this.sysMenuAll1()
     } else {
        this.sysMenuAll()
     }
   },
   pId(val) {
     this.page.parentId = val[val.length - 1]
   }
 },
 methods:{
     initData(){
         const url =systemApi.sysMenuList;
         this.$post(url,this.page).then(res =>{
             if(res.status === 1){
                 this.data=res;
                 this.tableData=res.data;
                  this.tableData.forEach(item =>{
                      item.children=null
                  })
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         }).catch(err =>{
           console.log(err)
         })
     },
     sysMenuAll(){
         const url =systemApi.sysMenuAll;
         this.$get(url).then(res =>{
             if(res.status === 1){
                const directoryObj={
                    id: '',
                    name:'全部'
                 }
                const menuArr=JSON.parse(JSON.stringify(res.data));
                const directoryData=JSON.parse(JSON.stringify(res.data));
                directoryData.unshift(directoryObj);
                // this.directoryProgramOptions=directoryData;
                menuArr.unshift({
                     id: '0',
                     name:'顶级菜单'
                })
                menuArr.forEach(item =>{
                    item.children=null
                })

                this.menuOptions=menuArr;
                  
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         }).catch(err =>{
            console.log(err)
         })
     },
     sysMenuAll1(){
         const url =systemApi.sysMenuAll;
         this.$get(url).then(res =>{
             if(res.status === 1){
                const directoryObj={
                    id: '',
                    name:'全部'
                 }
                const menuArr=JSON.parse(JSON.stringify(res.data));
                const directoryData=JSON.parse(JSON.stringify(res.data));
                // directoryData.unshift(directoryObj);
                // menuArr.unshift({
                //      id: '0',
                //      name:'顶级菜单'
                // })
                menuArr.forEach(item =>{
                  if(item.children) {
                    item.children.forEach(item1 => {
                      if(item1.children) {
                        item1.children=null
                      }
                    })
                  }
                    
                })

                this.directoryProgramOptions=JSON.parse(JSON.stringify(menuArr))
                this.directoryProgramOptions.unshift(directoryObj)
                this.menuOptions=menuArr
                  
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         }).catch(err =>{
            console.log(err)
         })
     },
     headInfo(item){
        this.pageTitle=item[1]
     },
     openAdd(){        
          this.sysMenuAll() 
          this.userDialogFormVisible = true;
          this.form={
                name: '',
                url: '',
                parentId: '',
                type: 0,
                sort: '',
                code:'null',
                id:undefined,
                icon: ''
        };
        this.menuSelectedOptions=[];
        this.isAdd=true;
     },
     search(){
        this.resetTemp();
        this.initData() ;
     },
     handleSizeChange(val){
        this.page.rows = val;
		this.initData()
     },
     handleCurrentChange(obj){
        this.page.page = obj;
        this.initData()
     },
     deleteRow(row){
        this.$confirm('确定删除该目录?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            this.userDel(row.id)
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });        
     },
     editRow(row){
        const obj={
                name: row.name,
                url: row.url,
                parentId: row.parentId,
                type: row.type,
                sort: row.sort,
                code:'null',
                id:row.id,
                icon: row.icon
        }
        if(row.type) {
          this.sysMenuAll1()
        } else {
            this.sysMenuAll()
        }
        this.form=obj;
        this.menuSelectedOptions=row.parentIds ? row.parentIds.split(',') : row.parentId.split(',');
        this.userDialogFormVisible = true; 
        this.isAdd=false;
     },
     create(formName){            
       this.$refs[formName].validate((valid) => {
          if (valid) {
            this.form.parentId=this.menuSelectedOptions[this.menuSelectedOptions.length -1]
            if(this.isAdd){
                  this.userAdd()
            }else{   
                 this.userEdit()             
                
            }           
                     
          } 
        });
     },
     cancel(formName){
        this.userDialogFormVisible = false;
        this.$refs[formName].resetFields();
     },
     handleDialogClose(){
        this.$refs['form'].resetFields();
        this.userDialogFormVisible = false;    
     },
     userAdd(){
        const url =systemApi.sysMenuAdd;
       this.$post(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                        this.userDialogFormVisible = false;
                        this.resetTemp()
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            }).catch(err =>{
                 console.log(err)
            })
     },
     userDel(id){
       const url =systemApi.sysMenuDel({
           id:id
       });
        this.$deletes(url)
        .then( res => {
                if(res.status === 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: res.message
                        
                        })
                    this.initData() ;
                }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                })
                }
                
        }).catch(err =>{
             console.log(err)
        })
     },
     userEdit(){
        const url =systemApi.sysMenuEdit;
        this.$put(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                        this.userDialogFormVisible = false;
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            }).catch(err =>{
                console.log(err)
            })
     },
     resetTemp(){
        this.page.page=1;
        this.page.rows=15;
     }
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss'
</style>



